import React from 'react'
import "./aboutus.css"
import im from "./../images/undraw_bookmarks_r6up.svg"

export default function Aboutus() {
    return (
        <section className="about">
            <div className="container">
                <h2>About Us</h2>
                <div className="cont">
                    <img src={im} alt="about" />
                    <div className="text">
                        <h3>Our Mission</h3>
                        <p>Eos cumque optio dolores excepturi rerum temporibus magni recusandae eveniet, totam omnis consectetur maxime quibusdam expedita dolorem dolor nobis dicta labore quaerat esse magnam unde, aperiam delectus! At maiores, itaque.</p>
                        <ul>
                            <li>Laborum enim quasi at modi</li>
                            <li>Ad at tempore</li>
                            <li>Labore quaerat esse</li>
                        </ul>
                        <button>Learn More</button>
                    </div>
                </div>
            </div>
        </section>
    )
}
