import React from 'react'
import "./features.css"
import Fbox from './fbox'

export default function Features() {
    return (
        <section className="features">
            <div className="container">
                <div className="text">
                    <h2>Imagine Features</h2>
                    <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Fuga quos quaerat sapiente nam, id vero.</p>
                </div>
                <div className="cont">
                    <Fbox classicon="fa-sync" title="Marketing Consulting" />
                    <Fbox classicon="fa-store-alt" title="Market Analysis" />
                    <Fbox classicon="fa-shopping-basket" title="Easy Purchase" />
                    <Fbox classicon="fa-cloud-download-alt" title="Free Updates" />
                    <Fbox classicon="fa-smile" title="100% Satistified" />
                    <Fbox classicon="fa-plug" title="Easy Plugin" />
                </div>
            </div>
        </section>
    )
}
