import React from 'react'
import "./fbox.css"

export default function Fbox(props) {
    return (
        <div className="box">
            <i className={`fas ${props.classicon}`}></i>
            <div className="text">
                <h3>{props.title}</h3>
                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Perferendis quis molestiae vitae eligendi at.</p>
                <a href="#">Learn More</a>
            </div>
        </div>
    )
}
